<template>
  <v-app id="main-view">
    <!-- <core-filter /> -->
    <core-toolbar />
    <v-content
      :style="'padding-left: 0px;'"
    >
      <v-fade-transition mode="out-in">
        <router-view />
      </v-fade-transition>
    </v-content>
    <div
      v-if="false"
      id="message-icon"
      @click="doOpenMessage()"
    >
      <div class="cursor">
        <v-icon>mdi-facebook-messenger</v-icon>
      </div>
    </div>
    <!-- <core-footer v-if="$route.meta.name !== 'Maps'" /> -->
    <Chatroom
      v-show="chatView"
      @snackbar="doSnackbar"
    />
    <div
      id="chat-icon"
      @click="doOpenChat()"
    >
      <div
        v-if="!chatView"
        :class="{ 'active': doReadStatus }"
        class="cursor"
      >
        <img :src="ChatIcon">
      </div>
      <!-- <div
        v-if="!chatView"
        class="cursor-no-hover text-white mr-1 active">
        <v-icon>mdi-message-processing</v-icon>
      </div> -->
      <div
        v-if="chatView"
        class="cursor text-white close"
      >
        <v-icon>mdi-close</v-icon>
      </div>
    </div>
    <div
      v-if="isTeacherCB || isStudentCB"
      id="chatLog"
    >
      <p
        v-for="(log, l) in chatLog"
        :key="'l'+l"
        :style="'color:'+log.value"
      >
        {{ log.text }}
      </p>
    </div>
    <core-footer />
  </v-app>
</template>

<script>
import { mapState } from 'vuex'
import store from '@/store'
import Chatroom from '../components/Plugins/Chatroom.vue'
import ChatIcon from '@/assets/img/chat_icon.png'
export default {
  name: 'MainView',
  components: {
    Chatroom
  },
  metaInfo () {
    return {
      title: this.appName
    }
  },
  beforeRouteEnter (to, from, next) {
    store.state.fromPath = from.fullPath
    store.state.toPath = to.fullPath
    next()
  },
  data: () => ({
    ChatIcon,
    chatView: false,
    chatLog: []
  }),
  computed: {
    ...mapState(['root', 'appName', 'fromPath', 'toPath', 'isProduction', 'dologinView', 'doChatView', 'doReadStatus']),
    userInfo () {
      return JSON.parse(localStorage.getItem('user'))
    },
    isTeacherCB () {
      if (this.userInfo) {
        if (this.userInfo.group.indexOf('test_teacher') > -1) {
          return true
        } else {
          return false
        }
      } else {
        return false
      }
    },
    isStudentCB () {
      if (this.userInfo) {
        if (this.userInfo.group.indexOf('test_student') > -1) {
          return true
        } else {
          return false
        }
      } else {
        return false
      }
    }
  },
  watch: {
    '$route.fullPath': function (newValue) {
      this.doGtagsPageview()
    },
    doChatView (val) {
      this.chatView = val
    },
    chatView (val) {
      this.$store.state.doChatView = val
    }
  },
  mounted () {
    this.doGtagsPageview()
  },
  methods: {
    doOpenMessage () {
      window.open('https://reurl.cc/XeDE93', '_blank')
    },
    doGtagsPageview () {
      this.$store.state.root = this.$http.defaults.baseURL
      this.$store.state.isProduction = this.root === 'https://www.quikok.com'
      if (this.isProduction) {
        this.$gtag.pageview({
          from_path: this.fromPath,
          page_path: this.toPath,
          hostname: this.root,
          user_id: this.userInfo ? this.userInfo.user_id : -1
        })
      }
    },
    doOpenChat () {
      if (localStorage.getItem('token')) {
        this.chatView = !this.chatView
      } else {
        this.$store.state.dologinView = true
      }
    },
    doSnackbar (val) {
      this.chatLog.push({ value: val[0], text: val[1] })
    }
  }
}
</script>
