      font-size: 50px;
      color: #ffffff;
    }
  }
}
#chat-icon{
  >div{
    box-shadow: 2px 1px 10px #707070;
    position: fixed;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    opacity: 1;
    z-index: 999;
    background-color: $chat-color;
    display: flex;
    justify-content: center;
    align-items: center;
    @media (max-width: 499px) {
      right: 0px;
      bottom: 0px;
    }
    @media (min-width: 500px) {
      right: 15px;
      bottom: 10px;
    }
    >i{
      font-size: 45px;
      color: #ffffff;
    }
    >img{
      width: 45px;
      height: 45px;
    }
    &.close{
      background-color: #7a7a7a;
      @media (max-width: 499px) {
        border-radius: 0 0 50% 50%;
      }
      @media (min-width: 500px) {
        border-radius: 50% 0 50% 50%;
      }
    }
    &.active{
      animation-name: chat_btn;
      animation-duration: 0.5s;
      animation-iteration-count:infinite;
      @media (max-width: 499px) {
        @keyframes chat_btn{
          0%{
            bottom: 0px;
            background-color: $chat-color;
          }
          50%{
            bottom: 10px;
            background-color: #fe4545;
          }
          100%{
            bottom: 0px;
            background-color: $chat-color;
          }
        }
      }
      @media (min-width: 500px) {
        @keyframes chat_btn{
          0%{
            bottom: 10px;
            background-color: $chat-color;
          }
          50%{
            bottom: 20px;
            background-color: #fe4545;
          }
          100%{
            bottom: 10px;
            background-color: $chat-color;
          }
        }
      }
    }
  }
}
#chatLog{
  position: fixed;
  word-break: break-all;
